<template>
  <el-drawer
    :title="title"
    :visible="value"
    append-to-body
    destroy-on-close
    :before-close="beforeClose"
    :close-on-press-escape="false"
    :direction="direction"
    :size="size"
    :wrapper-closable="false"
    custom-class="user-drawer"
  >
    <div v-loading="pageLoading">
      <el-form v-if="warehousePullList.length >= 2" ref="form" :model="query" inline class="form-box">
        <el-form-item prop="warehouseId" label="">
          <el-select v-model="query.warehouseId" filterable clearable placeholder="" style="width: 178px;" @change="selectWarehouse">
            <el-option
              v-for="(item,index) in warehousePullList"
              :key="index"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="">
          <el-button v-for="(loc, ind) in recommendStorageLocation" :key="ind" type="text" @click="chooseLocation(loc)">{{ loc.currentPosition }}</el-button>
        </el-form-item> -->
      </el-form>
      <el-scrollbar class="scroll-drawer" @wheel.native="handleScroll">
        <div class="draw-container">
          <!-- Tip  -->
          <el-tabs v-if="districtData.length" v-model="activeName" type="card" @tab-click="handleTabClick">
            <el-tab-pane
              v-for="(quItem, index) in districtData"
              :key="index"
              class="tabPanel"
              :name="`${quItem.warehouseId}-${quItem.id}`"
            >
              <!-- SAP -->
              <span slot="label">
                <span style="font-size: 18px;font-weight: bold;">{{ `${quItem.name}${quItem.level_dictText}` }}</span>
                <span style="color: #909399;">{{ quItem.sapName | sapKuStr }}</span>
              </span>
              <div v-if="subzoneData.length" class="huojia-sty">
                <!-- Tip  -->
                <div v-for="(hjItem, i) in subzoneData" :key="i">
                  <el-card :body-style="{ minHeight: '325px', width: getHjWidth(hjItem.children)}">
                    <div slot="header" class="clearfix">
                      <div style="text-align: center;cursor: pointer;" @click="chooseHuoJia(hjItem)">{{ hjItem.name }}</div>
                    </div>
                    <template v-if="hjItem.children.length">
                      <!-- Tip  -->
                      <template v-for="(cengItem, f) in hjItem.children">
                        <div :key="f" class="tier">
                          <div class="floor-name-sty" @click="chooseCeng(cengItem)">{{ cengItem.name }}</div>
                          <template>
                            <div class="ceng-wrapper">
                              <!-- Tip  -->
                              <div v-if="cengItem.children.length" class="parent">
                                <div v-for="(kuweiItem, l) in cengItem.children" :key="l" class="child" :style="{backgroundColor: !kuweiItem.empty || getCacheAllStorageId().some(c => c.includes(kuweiItem.storageId)) ? '#FFF5A7' : '#A4E49C'}">
                                  <!-- () -->
                                  <el-popover
                                    v-if="!kuweiItem.empty"
                                    :key="kuweiItem.id"
                                    :close-delay="0"
                                    :open-delay="300"
                                    placement="top"
                                    width="200"
                                    trigger="hover"
                                    popper-class="user-el-popover"
                                    :visible-arrow="false"
                                  >
                                    <!--  -->
                                    <el-descriptions :column="1" class="user-el-descriptions">
                                      <el-descriptions-item label="">
                                        {{ kuweiItem.materialCode || '' }}
                                      </el-descriptions-item>
                                      <el-descriptions-item label="">
                                        {{ kuweiItem.materialName || '' }}
                                      </el-descriptions-item>
                                      <el-descriptions-item label="">
                                        {{ kuweiItem.total || '' }}
                                      </el-descriptions-item>
                                      <el-descriptions-item label="">
                                        {{ kuweiItem.num || '' }}
                                      </el-descriptions-item>
                                    </el-descriptions>
                                    <div slot="reference" style="cursor: pointer;" @click="chooseKuwei(kuweiItem)">{{ kuweiItem.name }}</div>
                                  </el-popover>
                                  <!--  -->
                                  <div v-else style="cursor: pointer;" @click="chooseKuwei(kuweiItem)">
                                    {{ kuweiItem.name }}
                                  </div>
                                </div>
                              </div>
                              <!-- Tip  -->
                              <div v-else class="whole-floor" />
                              <div class="divider" />
                            </div>
                          </template>
                        </div>
                      </template>
                    </template>
                    <!-- Tip  -->
                    <template v-else>
                      <div class="entire-shelf" />
                    </template>
                  </el-card>
                </div>
              </div>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-scrollbar>
      <div v-if="false" class="drawer-footer">
        <el-button type="primary" @click="handleConfirm"> </el-button>
        <el-button @click="beforeClose"> </el-button>
      </div>
    </div>
  </el-drawer>
</template>

<script>
import { getWarehousePullListApi } from '@/apis/modules/base/basewmsWarehouse'
import { getChildNodeListApi, getBelowTheZoneListApi } from '@/apis/modules/base/baseWarehouseLocation'
import { getBarcodeListApi } from '@/apis/modules/warehouse/inventory'
import { requestWrapper, promiseWrapper } from '@/utils/utils'
export default {
  name: 'AddBomNodeDrawerComs',
  components: {
  },
  filters: {
    sapKuStr(name) {
      let str = ''
      name.replace(/\((.*?)\)/g, (match, content) => {
        str = content
      })
      return `(${str})`
    }
  },
  props: {
    value: {
      required: true,
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    direction: {
      type: String,
      default: 'btt'
    },
    size: {
      type: String,
      default: '95%'
    },
    // bindRelation: {
    //   type: Array,
    //   default: () => ([])
    // },
    materialCode: {
      type: [String, Number],
      default: ''
    },
    selectedInfo: {
      type: Object,
      default: () => ({})
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      activeName: '',
      warehousePullList: [],
      query: {
        warehouseId: ''
      },
      districtData: [], // 
      materialInfo: {},
      pageLoading: false,
      subzoneData: [], // 
      recommendStorageLocation: [] // 
    }
  },
  computed: {
  },
  mounted() {
    this.getWarehousePullList()
    // this.getRecommendStorageLocation()
  },
  methods: {
    // 
    getRecommendStorageLocation() {
      const params = {
        materialCode: this.extraInfo.materialCode,
        terminal: 'pc'
      }
      return new Promise((resolve, reject) => {
        getBarcodeListApi(params)
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.recommendStorageLocation = data.map(c => ({ ...c, isAuth: true }))
            }
            resolve(res)
          })
          .catch(err => {
            console.log('err: ', err)
            reject(err)
          })
      })
    },
    handleScroll() {
    },
    // 
    handleConfirm() {},
    // 
    async chooseDistrict({ isAuth, currentPosition, storageId }) {
      const diag = await promiseWrapper(this.$confirm(`${currentPosition}?`, '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false,
        distinguishCancelAndClose: true,
        closeOnPressEscape: false,
        closeOnClickModal: false,
        showClose: false // 
      }))
      // Tip ()
      if (!diag.success) {
        this.$emit('input', false)
        return
      }
      // Tip 
      if (!isAuth) {
        this.$notify.warning({
          title: '',
          message: ''
        })
        return
      }
      this.$emit('confirm', {
        currentPosition,
        storageId
      })
      this.$emit('input', false)
    },
    // 
    beforeClose(done) {
      for (const item of this.districtData) {
        if (`${item.warehouseId}-${item.id}` === this.activeName) {
          this.chooseDistrict(item)
          return
        }
      }
      this.$emit('input', false)
      typeof done === 'function' && done()
    },
    // 
    getWarehousePullList() {
      return new Promise((resolve, reject) => {
        // sapid
        getWarehousePullListApi({ warehouseId: this.extraInfo.warehouseId }).then(res => {
          const { code, data } = res
          if (!code) {
            this.warehousePullList = data || []
            if (data.length) {
              this.query.warehouseId = data[0].id
              this.selectWarehouse(data[0].id)
            }
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // 
    async getBelowTheZoneList(warehouseId, id) {
      const res = await requestWrapper(getBelowTheZoneListApi({
        warehouseSapId: this.extraInfo.warehouseId,
        isStrategic: this.extraInfo.isStrategic, // 
        warehouseId, // id
        parentId: id // id
      }))
      if (!res.success) return
      this.subzoneData = res.data
    },
    // 
    async selectWarehouse(val) {
      const res = await requestWrapper(getChildNodeListApi({
        warehouseSapId: this.extraInfo.warehouseId,
        isStrategic: this.extraInfo.isStrategic, // 
        warehouseId: val, // id
        parentId: null // id
      }))
      if (!res.success) return
      this.districtData = res.data
      this.activeName = !this.districtData.length ? '' : `${this.districtData[0].warehouseId}-${this.districtData[0].id}`
      this.districtData.length && this.getBelowTheZoneList(this.districtData[0].warehouseId, this.districtData[0].id)
    },
    // Tip 
    handleTabClick(tab, event) {
      this.getBelowTheZoneList(this.activeName.split('-')[0], this.activeName.split('-')[1])
    },
    // Tip 
    chooseHuoJia(item) {
      if (!item.empty) {
        if (item.materialCode) {
          const materialCodes = item.materialCode.split(',')
          // Tip  
          if (!materialCodes.includes(this.materialCode)) {
            this.$notify.warning({
              title: '',
              message: ''
            })
            return
          }
        }
      } else {
        if (this.findStorageIdMaterial(item.storageId)) {
          const isBindSameMaterial = this.findStorageIdMaterial(item.storageId) === this.materialCode
          // Tip  
          if (!isBindSameMaterial) {
            this.$notify.warning({
              title: '',
              message: ''
            })
            return
          }
        }
      }
      this.chooseLocation(item)
    },
    // Tip 
    chooseCeng(item) {
      if (!item.empty) {
        if (item.materialCode) {
          const materialCodes = item.materialCode.split(',')
          // Tip  
          if (!materialCodes.includes(this.materialCode)) {
            this.$notify.warning({
              title: '',
              message: ''
            })
            return
          }
        }
      } else {
        if (this.findStorageIdMaterial(item.storageId)) {
          const isBindSameMaterial = this.findStorageIdMaterial(item.storageId) === this.materialCode
          //  
          if (!isBindSameMaterial) {
            this.$notify.warning({
              title: '',
              message: ''
            })
            return
          }
        }
      }
      this.chooseLocation(item)
    },
    // Tip 
    chooseKuwei(item) {
      if (!item.empty) {
        if (item.materialCode) {
          const materialCodes = item.materialCode.split(',')
          //  
          if (!materialCodes.includes(this.materialCode)) {
            this.$notify.warning({
              title: '',
              message: ''
            })
            return
          }
        }
      } else {
        if (this.findStorageIdMaterial(item.storageId)) {
          const isBindSameMaterial = this.findStorageIdMaterial(item.storageId) === this.materialCode
          //  
          if (!isBindSameMaterial) {
            this.$notify.warning({
              title: '',
              message: ''
            })
            return
          }
        }
      }
      this.chooseLocation(item)
    },
    // id
    getBindRelation(key) {
      const obj = JSON.parse(sessionStorage.getItem('bindRelation'))
      return obj[`${key}`]
    },
    // (idid)
    hasBindRelation(key, val) {
      // id
      const arr = this.getBindRelation(key)
      return arr.includes(val)
    },
    // 
    findStorageIdMaterial(storageId) {
      const ind = this.getCacheAllStorageId().findIndex(c => c.includes(storageId))
      if (ind !== -1) {
        return this.getCacheAllmaterial()[ind]
      }
      return ''
    },
    // 
    getCacheAllmaterial() {
      const obj = JSON.parse(sessionStorage.getItem('bindRelation'))
      return Object.keys(obj)
    },
    // 
    getCacheAllStorageId() {
      const obj = JSON.parse(sessionStorage.getItem('bindRelation'))
      return Object.values(obj)
    },
    async chooseLocation(item) {
      const diag = await promiseWrapper(this.$confirm(`${item.currentPosition}?`, '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }))
      if (!diag.success) return
      // 
      const isCache = this.getCacheAllmaterial().includes(`${this.materialCode}`)
      if (this.selectedInfo.storageId !== item.storageId) {
        // 
        if (isCache) {
          const flag = this.hasBindRelation(this.materialCode, this.selectedInfo.storageId)
          // 
          // const bool = this.getBindRelation(this.materialCode).includes(this.selectedInfo.storageId)
          if (flag) {
          // 

            // 
            const num = this.getBindRelation(this.materialCode).filter(c => c === item.storageId).length

            // 
            const ind = this.getBindRelation(this.materialCode).findIndex(c => c === item.storageId)
            let arr = []
            if (num === 1) {
              arr = this.getBindRelation(this.materialCode).filter(c => c !== item.storageId)
            }

            // 
            if (num > 1) {
              if (ind !== -1) {
                arr = this.getBindRelation(this.materialCode).filter((c, d) => d !== ind)
              }
            }

            if (!arr.length) {
              this.removeBindRelation(this.materialCode) // 
            } else {
              const obj = JSON.parse(sessionStorage.getItem('bindRelation'))
              obj[`${this.materialCode}`] = arr
              const str = JSON.stringify(obj)
              sessionStorage.setItem('bindRelation', str)
            }
          // obj[`${this.materialCode}`] = this.getBindRelation(this.materialCode).filter(item => item !== this.selectedInfo.storageId)
          // const str = JSON.stringify(obj)
          // sessionStorage.setItem('bindRelation', str)
          }
        }
        if (!item.isAuth) {
          this.$notify.warning({
            title: '',
            message: ''
          })
          return
        }
        this.$emit('confirm', {
          currentPosition: item.currentPosition,
          storageId: item.storageId
        })
        this.$emit('input', false)
      } else {
        this.$emit('input', false)
      }
    },
    // 
    removeBindRelation(key) {
      const obj = JSON.parse(sessionStorage.getItem('bindRelation'))
      if (obj && obj[`${key}`]) {
        delete obj[`${key}`]
        const str = JSON.stringify(obj)
        sessionStorage.setItem('bindRelation', str)
      }
    },
    getHjWidth(arr = []) {
      if (arr && arr.length) { // 
        const is600 = arr.some(item => {
          return item.children && item.children.length > 5 && item.children.length <= 10
        })
        const is900 = arr.some(item => {
          return item.children && item.children.length > 10 && item.children.length <= 15
        })
        const is1200 = arr.some(item => {
          return item.children && item.children.length > 15 && item.children.length <= 20
        })
        if (is600) return '660px'
        if (is900) return '960px'
        if (is1200) return '1260px'
        return '360px'
      }
      return '360px'
    }
  }
}
</script>

<style lang='scss' scoped>
::v-deep:focus {
    outline: 0;
}
.form-box {
  padding: 0 20px;
  box-sizing: border-box;
}
.draw-container{
    padding: 0 20px;
}
.scroll-drawer {
    height: calc(100vh - 160px);
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
}
::v-deep .user-drawer.el-drawer {
    width: 100%;
}
// 
.huojia-sty {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    & > div {
        margin: 0 30px 15px;
    }
}
// 
.tier {
    display: flex;
    align-items: center;
    margin: 5px 0 10px;
    width: 100%;
    // 
    .floor-name-sty {
        width: 50px;
        height: 50px;
        margin: 0 5px;
        text-align: center;
        line-height: 50px;
        background-color: #a3a2a2;
        cursor: pointer;
    }
    // 
    .ceng-wrapper {
        position: relative;
        width: calc(100% - 60px);
        // 
        .parent {
            display: flex;
            align-items: center;
            .child {
                width: 50px;
                height: 50px;
                text-align: center;
                line-height: 50px;
                background: #a3a2a2;
                margin: 0 5px;
                border-radius: 10px;
                font-size: 13px;
            }
        }
        // 
        .whole-floor {
            width: 100%;
            height: 50px;
            background-color: #A4E49C;
        }
        // 
        .divider {
            width: 100%;
            height: 1px;
            background-color: #BEBEBE;
            box-shadow: 2px 2px 2px #BEBEBE;
            position: absolute;
            left: 0;
            bottom: -3px;
            margin: 0;
        }
    }
}
.entire-shelf {
    width: 100%;
    min-height: 325px;
    // height: 100%;
    background-color: #A4E49C;
}
</style>

<style lang="scss">
.el-popover.user-el-popover {
    border-radius: 10px;
    background-color: #E0F4FF;
    opacity: .9;
    .user-el-descriptions {
        &.el-descriptions {
            .el-descriptions__body {
                background-color: #E0F4FF !important;
                opacity: .9;
            }
        }
    }
}
.drawer-footer {
  text-align: right;
}
.tabPanel {

}
.no-data {
width: 200px;
height: 150px;
text-align: center;
line-height: 150px;
color: #409EFF;
}
</style>

