import request from '@/apis/request.wms'

// 
export function getListApi(data) {
  return request({
    url: '/basic/wmsWarehouseStorageLocation/pageList',
    method: 'post',
    data
  })
}
// 
export function getPullListItemApi(data) {
  return request({
    url: '/basic/wmsWarehouseStorageLocation/listItem',
    method: 'post',
    data
  })
}
