import request from '@/apis/request.wms'

// 
export function getInfoApi(data) {
  return request({
    url: '/enterWarehouse/wmsStockIn/deliveryReceiptDetail',
    method: 'post',
    data
  })
}

// 
export function getListApi(data) {
  return request({
    url: '/enterWarehouse/wmsStockIn/deliveryMaterialPageList',
    method: 'post',
    data
  })
}

// (stockInId  id)
export function getStockinMaterialListApi(data = {}) {
  return request({
    url: '/enterWarehouse/wmsStockIn/materialAndBarcodeWithoutStorageId',
    method: 'post',
    data
  })
}

// (deliveryMaterialId  ID stockInId id storageId ID)
export function materialInStorageApi(data = {}) {
  return request({
    url: '/enterWarehouse/wmsStockIn/saveBarcodeStoragePositionWithoutStorageId',
    method: 'post',
    data
  })
}

// (webList)
export function barcodeInStorageApi(data = {}) {
  return request({
    url: '/enterWarehouse/wmsStockIn/addPutInStorage',
    method: 'post',
    data
  })
}
