export default {
  methods: {
    // 
    getCacheAllmaterial() {
      const obj = JSON.parse(sessionStorage.getItem('bindRelation'))
      return Object.keys(obj)
    },
    // 
    getCacheAllStorageId() {
      const obj = JSON.parse(sessionStorage.getItem('bindRelation'))
      return Object.values(obj)
    },
    // id
    getBindRelation(key) {
      const obj = JSON.parse(sessionStorage.getItem('bindRelation'))
      return obj[`${key}`]
    },
    // (idid)
    hasBindRelation(key, val) {
      // id
      const arr = this.getBindRelation(key)
      return arr.includes(val)
    },
    // 
    findStorageIdMaterial(storageId) {
      const ind = this.getCacheAllStorageId().findIndex(c => c.includes(storageId))
      if (ind !== -1) {
        return this.getCacheAllmaterial()[ind]
      }
      return ''
    },
    // 
    materialIsCache(materialCode) {
      return this.getCacheAllmaterial().includes(`${materialCode}`)
    },
    // key:  valid
    setBindRelation(key, val) {
      const obj = JSON.parse(sessionStorage.getItem('bindRelation'))
      if (obj) {
        if (obj[`${key}`]) {
          obj[`${key}`] = [...obj[`${key}`], val]
        } else {
          obj[`${key}`] = [val]
        }
        const str = JSON.stringify(obj)
        sessionStorage.setItem('bindRelation', str)
      }
    },

    // 
    removeBindRelation(key) {
      const obj = JSON.parse(sessionStorage.getItem('bindRelation'))
      if (obj && obj[`${key}`]) {
        delete obj[`${key}`]
        const str = JSON.stringify(obj)
        sessionStorage.setItem('bindRelation', str)
      }
    }
  }
}
