<template>
  <el-drawer
    :title="title"
    :visible="value"
    append-to-body
    destroy-on-close
    :before-close="beforeClose"
    :close-on-press-escape="false"
    :direction="direction"
    :size="size"
    :wrapper-closable="false"
    custom-class="user-drawer"
    @open="initSelect"
  >
    <template v-if="value">
      <div slot="title" style="display: flex;align-items: center;">
        <span>{{ title }}</span>
        <el-button v-if="false" type="primary" style="margin-left: 45px;" @click="chooseDistrict"></el-button>
      </div>
      <div class="choose-material-box">
        <span class="box-title"></span>
        <template v-if="materialList.length">
          <el-dropdown v-for="(materialInfo1, k) in materialList" :key="k" split-button :hide-on-click="false" trigger="click" type="primary" class="dropdown-custom-sty" :class="{noShowBtn: materialInfo1.barcodeList.length <= 1, 'checked-dropdown': materialInfo1.checked}" @click="handleDropdownClick(materialInfo1)" @command="handleDropdownItemClick">
            {{ materialInfo1.materialName }}
            <el-dropdown-menu slot="dropdown">
              <el-scrollbar style="height: 200px;">
                <el-dropdown-item v-for="(barcodeInfo, i) in materialInfo1.barcodeList" :key="i" :command="{...Object.assign(materialInfo1, {currentItem: barcodeInfo})}" divided :class="{'selected-dropdown-item': barcodeInfo.checked}">{{ barcodeInfo.barCode }}</el-dropdown-item>
              </el-scrollbar>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
        <div v-else style="margin-bottom: 10px;text-align: center;color: #909399;font-size: 12px;">
          
        </div>
      </div>
      <el-form v-if="warehousePullList.length >= 2" ref="form" :model="query" inline class="form-box">
        <el-form-item prop="warehouseId" label="">
          <el-select v-model="query.warehouseId" filterable clearable placeholder="" style="width: 178px;" @change="selectWarehouse">
            <el-option
              v-for="(item,index) in warehousePullList"
              :key="index"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
      </el-form>
      <el-scrollbar class="scroll-drawer">
        <div class="draw-container">
          <!--  -->
          <el-tabs v-if="districtData.length" v-model="activeName" type="card" @tab-click="handleTabClick">
            <el-tab-pane
              v-for="(quItem, index) in districtData"
              :key="index"
              class="tabPanel"
              :name="`${quItem.warehouseId}-${quItem.id}`"
            >
              <!-- SAP -->
              <span slot="label">
                <span style="font-size: 18px;font-weight: bold;">{{ `${quItem.name}${quItem.level_dictText}` }}</span>
                <span style="color: #909399;">{{ quItem.sapName | sapKuStr }}</span>
              </span>
              <div v-if="subzoneData.length" class="huojia-sty">
                <!--  -->
                <div v-for="(hjItem, i) in subzoneData" :key="i">
                  <el-card :body-style="{ minHeight: '325px', width: getHjWidth(hjItem.children)}">
                    <div slot="header" class="clearfix">
                      <div style="text-align: center;cursor: pointer;" @click="chooseHuoJia(hjItem)">{{ hjItem.name }}</div>
                    </div>
                    <template v-if="hjItem.children.length">
                      <!--  -->
                      <template v-for="(cengItem, f) in hjItem.children">
                        <div :key="f" class="tier">
                          <div class="floor-name-sty" @click="chooseCeng(cengItem)">{{ cengItem.name }}</div>
                          <template>
                            <div class="ceng-wrapper">
                              <!--  -->
                              <div v-if="cengItem.children.length" class="parent">
                                <div v-for="(kuweiItem, l) in cengItem.children" :key="l" class="child" :style="{backgroundColor: !kuweiItem.empty || getCacheAllStorageId().some(c => c.includes(kuweiItem.storageId)) ? '#FFF5A7' : '#A4E49C'}">
                                  <!-- () -->
                                  <el-popover
                                    v-if="!kuweiItem.empty"
                                    :key="kuweiItem.id"
                                    :close-delay="0"
                                    :open-delay="300"
                                    placement="top"
                                    width="200"
                                    trigger="hover"
                                    popper-class="user-el-popover"
                                    :visible-arrow="false"
                                  >
                                    <!-- @show="getMaterialsInformation(kuweiItem, `popover-${index}-${i}-${f}-${l}`)" -->
                                    <!--  -->
                                    <el-descriptions :column="1" class="user-el-descriptions">
                                      <el-descriptions-item label="">
                                        {{ kuweiItem.materialCode || '' }}
                                      </el-descriptions-item>
                                      <el-descriptions-item label="">
                                        {{ kuweiItem.materialName || '' }}
                                      </el-descriptions-item>
                                      <el-descriptions-item label="">
                                        {{ kuweiItem.total || '' }}
                                      </el-descriptions-item>
                                      <el-descriptions-item label="">
                                        {{ kuweiItem.num || '' }}
                                      </el-descriptions-item>
                                    </el-descriptions>
                                    <div slot="reference" style="cursor: pointer;" @click="chooseKuwei(kuweiItem)">{{ kuweiItem.name }}</div>
                                  </el-popover>
                                  <!--  -->
                                  <div v-else style="cursor: pointer;" @click="chooseKuwei(kuweiItem)">
                                    {{ kuweiItem.name }}
                                  </div>
                                </div>
                              </div>
                              <!--  -->
                              <div v-else class="whole-floor" />
                              <div class="divider" />
                            </div>
                          </template>
                        </div>
                      </template>
                    </template>
                    <!--  -->
                    <template v-else>
                      <div class="entire-shelf" />
                    </template>
                  </el-card>
                </div>
              </div>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-scrollbar>
    </template>
    <div v-if="false" class="drawer-footer">
      <el-button type="primary" @click="handleConfirm"> </el-button>
      <el-button @click="beforeClose"> </el-button>
    </div>
  </el-drawer>
</template>

<script>
import { getWarehousePullListApi, getMaterialsInformationApi } from '@/apis/modules/base/basewmsWarehouse'
import { getChildNodeListApi, getBelowTheZoneListApi } from '@/apis/modules/base/baseWarehouseLocation'
import { getStockinMaterialListApi, materialInStorageApi, barcodeInStorageApi } from '@/apis/modules/in-storage/info/Receipt'
import { requestWrapper, promiseWrapper } from '@/utils/utils'
import cacheLocation from './cacheLocation'
export default {
  name: 'AddBomNodeDrawerComs',
  components: {
  },
  filters: {
    sapKuStr(name) {
      let str = ''
      name.replace(/\((.*?)\)/g, (match, content) => {
        str = content
      })
      return `(${str})`
    }
  },
  mixins: [cacheLocation],
  props: {
    value: {
      required: true,
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    direction: {
      type: String,
      default: 'btt'
    },
    size: {
      type: String,
      default: '95%'
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      activeName: '',
      warehousePullList: [],
      query: {
        warehouseId: ''
      },
      districtData: [], // 
      materialInfo: {},
      subzoneData: [], // 
      materialList: [],
      materialForm: {},
      inStorageFlag: '', // '1'   '2':  
      chooseMaterialList: [], // 
      chooseBarcodeList: [] // 
    }
  },
  computed: {
    isExistMaterial() {
      return !!this.materialList.length
    }
  },
  watch: {
  },
  mounted() {
  },
  methods: {
    initSelect() {
      this.inStorageFlag = '' // 
      this.chooseMaterialList = [] // 
      this.chooseBarcodeList = [] // 
      this.getStockinMaterialList() // 
      this.getWarehousePullList() // 
    },
    // 
    getStockinMaterialList() {
      return new Promise((resolve, reject) => {
        getStockinMaterialListApi({ stockInId: this.extraInfo.stockInId })
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.materialList = data || []
              this.materialList = this.materialList.map(item => {
                const barcodeList = item.barcodeList.map(c => ({ ...c, checked: false }))
                return { ...Object.assign({}, item, { checked: false }), barcodeList }
              })
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    // 
    resetAllCheckedStatus() {
      this.materialList.forEach(mmm => {
        mmm.checked = false
        mmm.barcodeList.forEach(bbb => {
          bbb.checked = false
        })
      })
    },
    // 
    resetMaterialCheckedStatus() {
      this.materialList.forEach(mmm => {
        mmm.checked = false
      })
    },
    // 
    resetBarcodeCheckedStatus() {
      this.materialList.forEach(mmm => {
        mmm.barcodeList.forEach(bbb => {
          bbb.checked = false
        })
      })
    },
    // 
    handleDropdownClick(item) {
      this.inStorageFlag = '1' // 
      this.$nextTick(() => {
        this.resetBarcodeCheckedStatus()
        // 
        if (this.chooseMaterialList.some(c => c.materialCode !== item.materialCode)) {
          this.resetAllCheckedStatus()
          item.checked = true
          this.chooseMaterialList = [item]
        } else { // 
          item.checked = !item.checked
          if (item.checked) {
            this.chooseMaterialList = [item]
          } else {
            this.chooseMaterialList = []
          }
        }
      })
    },
    // 
    handleDropdownItemClick(item) {
      this.inStorageFlag = '2' // 
      this.$nextTick(() => {
        this.resetMaterialCheckedStatus()
        // 
        if (this.chooseBarcodeList.some(c => c.materialCode !== item.materialCode)) {
          // 
          this.resetAllCheckedStatus()
          // 
          item.currentItem.checked = true
          this.chooseBarcodeList = [item]
        } else { // 
          item.currentItem.checked = !item.currentItem.checked
          // 
          if (item.currentItem.checked) {
            this.chooseBarcodeList.push(item)
          } else {
            this.chooseBarcodeList = this.chooseBarcodeList.filter(aaa => aaa.currentItem.barCode !== item.currentItem.barCode)
          }
        }
      })
    },
    // 
    async chooseDistrict() {
      const { isAuth, currentPosition, storageId } = this.districtData.find(c => `${c.warehouseId}-${c.id}` === this.activeName)
      if (!this.inStorageFlag) {
        this.$notify.warning({
          title: '',
          message: ''
        })
        return
      }
      const diag = await promiseWrapper(this.$confirm(`${currentPosition}?`, '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false,
        distinguishCancelAndClose: true,
        closeOnPressEscape: false,
        closeOnClickModal: false,
        showClose: false // 
      }))
      // ()
      if (!diag.success) {
        this.$emit('input', false)
        return
      }
      // 
      if (!isAuth) {
        this.$notify.warning({
          title: '',
          message: ''
        })
        return
      }
      this.inStorageFlag === '1' ? this.materialInStorage(storageId) : this.barcodeInStorage(storageId)
    },
    // 
    getWarehousePullList() {
      return new Promise((resolve, reject) => {
        getWarehousePullListApi({
          warehouseId: this.extraInfo.warehouseSapId
        })
          .then(res => {
            const { code, data } = res
            if (!code) {
              this.warehousePullList = data || []
              if (data.length) {
                this.query.warehouseId = data[0].id
                this.selectWarehouse(data[0].id)
              }
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    // 
    async getBelowTheZoneList(warehouseId, id) {
      const res = await requestWrapper(getBelowTheZoneListApi({
        isStrategic: this.extraInfo.isStrategic, // 
        warehouseId, // id
        parentId: id, // id
        warehouseSapId: this.extraInfo.warehouseSapId
      }))
      if (!res.success) return
      this.subzoneData = res.data
    },
    // 
    async selectWarehouse(val) {
      const res = await requestWrapper(getChildNodeListApi({
        isStrategic: this.extraInfo.isStrategic, // 
        warehouseId: val, // id
        parentId: null, // id
        warehouseSapId: this.extraInfo.warehouseSapId
      }))
      if (!res.success) return
      this.districtData = res.data
      this.activeName = !this.districtData.length ? '' : `${this.districtData[0].warehouseId}-${this.districtData[0].id}`
      this.districtData.length && this.getBelowTheZoneList(this.districtData[0].warehouseId, this.districtData[0].id)
    },
    // 
    async getMaterialsInformation(material, ref) {
      for (const key in this.$refs) {
        // 
        if (key.indexOf('popover-') !== -1) {
          if (key !== ref) {
            this.$refs[key][0].doClose()
          }
        }
      }
      const { storageId } = material
      const res = await requestWrapper(getMaterialsInformationApi({ storageId }))
      if (!res.success) return
      const { data } = res
      const materialCode = [] // 
      const materialName = [] // 
      const supplierName = [] // 
      const total = [] // 
      const num = [] // 
      data.forEach(item => {
        materialCode.push(item.materialCode)
        materialName.push(item.materialName)
        supplierName.push(item.supplierName)
        total.push(item.total)
        num.push(item.num)
      })
      this.materialInfo = {
        materialCode: materialCode.join(), // 
        materialName: materialName.join(), // 
        supplierName: supplierName.join(), // 
        total: total.join(), // 
        num: num.join() // 
      }
    },
    // 
    materialInStorage(storageId = '') {
      const params = {
        stockInId: this.extraInfo.stockInId, // id
        deliveryMaterialId: this.chooseMaterialList[0].deliveryMaterialId, // id
        storageId // ID
      }
      return new Promise((resolve, reject) => {
        materialInStorageApi(params)
          .then(res => {
            const { code } = res
            if (!code) {
              // 
              this.getStockinMaterialList()
                .then(res => {
                  this.$notify.success({
                    title: '',
                    message: ``
                  })
                  // 
                  const materialCode = this.getCurrentMaterial()
                  this.setBindRelation(materialCode, storageId)
                  // 
                  this.chooseMaterialList = []
                  this.$emit('confirm')
                })
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    // 
    barcodeInStorage(storageId = '') {
      const wsbList = this.chooseBarcodeList.map(c => {
        return {
          stockInId: this.extraInfo.stockInId, // id
          deliveryMaterialId: c.deliveryMaterialId, // id
          deliveryMaterialDetailId: c.currentItem.id,
          storageId // ID
        }
      })
      const params = {
        wsbList
      }
      return new Promise((resolve, reject) => {
        barcodeInStorageApi(params)
          .then(res => {
            const { code } = res
            if (!code) {
              // 
              this.getStockinMaterialList()
                .then(res => {
                  this.$notify.success({
                    title: '',
                    message: ``
                  })
                  // 
                  const materialCode = this.getCurrentMaterial()
                  this.setBindRelation(materialCode, storageId)
                  // 
                  this.chooseBarcodeList = []
                  this.$emit('confirm')
                })
            }
            resolve(res)
          })
          .catch(err => {
            reject(err)
          })
      })
    },
    // 
    handleIsSelectedMaterialOrBarcode() {
      if (!this.inStorageFlag) {
        this.$notify.warning({
          title: '',
          message: ''
        })
        return false
      }
      if (this.inStorageFlag === '1' && !this.chooseMaterialList.length) {
        this.$notify.warning({
          title: '',
          message: ''
        })
        return false
      }
      if (this.inStorageFlag === '2' && !this.chooseBarcodeList.length) {
        this.$notify.warning({
          title: '',
          message: ''
        })
        return false
      }
      return true
    },
    // 
    getCurrentMaterial() {
      let materialCode = ''
      if (this.inStorageFlag === '1' && this.chooseMaterialList.length) {
        materialCode = this.chooseMaterialList[0].materialCode
      }
      if (this.inStorageFlag === '2' && this.chooseBarcodeList.length) {
        materialCode = this.chooseBarcodeList[0].materialCode
      }
      return materialCode
    },
    // 
    handleTabClick(tab, event) {
      this.getBelowTheZoneList(this.activeName.split('-')[0], this.activeName.split('-')[1])
    },
    // 
    chooseHuoJia(item) {
      this.$nextTick(() => {
        if (!this.isExistMaterial) {
          this.$notify.warning({
            title: '',
            message: ''
          })
          return
        }
        const isSelectedMaterialOrBarcode = this.handleIsSelectedMaterialOrBarcode()
        if (!isSelectedMaterialOrBarcode) return
        // 
        if (!item.empty) {
          if (item.materialCode) {
            const materialCodes = item.materialCode.split(',')
            const materialCode = this.getCurrentMaterial()
            //  
            if (!materialCodes.includes(materialCode)) {
              this.$notify.warning({
                title: '',
                message: ''
              })
              return
            }
          }
        }
        this.chooseLocation(item)
      })
    },
    // 
    chooseCeng(item) {
      this.$nextTick(() => {
        if (!this.isExistMaterial) {
          this.$notify.warning({
            title: '',
            message: ''
          })
          return
        }
        const isSelectedMaterialOrBarcode = this.handleIsSelectedMaterialOrBarcode()
        if (!isSelectedMaterialOrBarcode) return
        // 
        if (!item.empty) {
          if (item.materialCode) {
            const materialCodes = item.materialCode.split(',')
            const materialCode = this.getCurrentMaterial()
            //  
            if (!materialCodes.includes(materialCode)) {
              this.$notify.warning({
                title: '',
                message: ''
              })
              return
            }
          }
        }
        this.chooseLocation(item)
      })
    },
    // 
    chooseKuwei(item) {
      this.$nextTick(() => {
        if (!this.isExistMaterial) {
          this.$notify.warning({
            title: '',
            message: ''
          })
          return
        }
        const isSelectedMaterialOrBarcode = this.handleIsSelectedMaterialOrBarcode()
        if (!isSelectedMaterialOrBarcode) return
        const materialCode = this.getCurrentMaterial()
        // 
        if (!item.empty) {
          if (item.materialCode) {
            const materialCodes = item.materialCode.split(',')
            //  
            if (!materialCodes.includes(materialCode)) {
              this.$notify.warning({
                title: '',
                message: ''
              })
              return
            }
          }
        } else {
          // 
          if (this.findStorageIdMaterial(item.storageId)) {
            // 
            const isBindSameMaterial = this.findStorageIdMaterial(item.storageId) === materialCode
            // 
            if (!isBindSameMaterial) {
              this.$notify.warning({
                title: '',
                message: ''
              })
              return
            }
          }
        }
        this.chooseLocation(item)
      })
    },
    // 
    async chooseLocation(item) {
      const diag = await promiseWrapper(this.$confirm(`${item.currentPosition}?`, '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }))
      if (!diag.success) return
      if (!item.isAuth) {
        this.$notify.warning({
          title: '',
          message: ''
        })
        return
      }
      if (this.inStorageFlag === '1') {
        this.materialInStorage(item.storageId)
          .then(res => {
            item.empty = false
          })
          .catch(err => {
            console.log('err: ', err)
          })
      }
      if (this.inStorageFlag === '2') {
        this.barcodeInStorage(item.storageId)
          .then(res => {
            item.empty = false
          })
          .catch(err => {
            console.log('err: ', err)
          })
      }
    },
    getHjWidth(arr = []) {
      if (arr && arr.length) { // 
        const is600 = arr.some(item => {
          return item.children && item.children.length > 5 && item.children.length <= 10
        })
        const is900 = arr.some(item => {
          return item.children && item.children.length > 10 && item.children.length <= 15
        })
        const is1200 = arr.some(item => {
          return item.children && item.children.length > 15 && item.children.length <= 20
        })
        if (is600) return '660px'
        if (is900) return '960px'
        if (is1200) return '1260px'
        return '360px'
      }
      return '360px'
    },
    // 
    handleConfirm() {
    },
    // 
    beforeClose(done) {
      const arr = this.materialList.filter(c => c.isPartBarcodeHasStorageId)
      if (arr.length) {
        this.$notify.warning({
          title: '',
          message: `${arr.map(c => c.materialName).join()}`
        })
        return
      }
      this.$emit('input', false)
      typeof done === 'function' && done()
    }
  }
}
</script>

<style lang='scss' scoped>
::v-deep:focus {
    outline: 0;
}
.form-box {
  padding: 0 20px;
  box-sizing: border-box;
}
.draw-container{
    padding: 0 20px;
}
.scroll-drawer {
    height: calc(100vh - 160px);
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
    overflow-x: hidden;
}
::v-deep .user-drawer.el-drawer {
    width: 100%;
}
// 
.huojia-sty {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    & > div {
        margin: 0 30px 15px;
    }
}
// 
.tier {
    display: flex;
    align-items: center;
    margin: 5px 0 10px;
    width: 100%;
    // 
    .floor-name-sty {
        width: 50px;
        height: 50px;
        margin: 0 5px;
        text-align: center;
        line-height: 50px;
        background-color: #a3a2a2;
        cursor: pointer;
    }
    // 
    .ceng-wrapper {
        position: relative;
        width: calc(100% - 60px);
        // 
        .parent {
            display: flex;
            align-items: center;
            .child {
                width: 50px;
                height: 50px;
                text-align: center;
                line-height: 50px;
                background: #a3a2a2;
                margin: 0 5px;
                border-radius: 10px;
                font-size: 13px;
            }
        }
        // 
        .whole-floor {
            width: 100%;
            height: 50px;
            background-color: #A4E49C;
        }
        // 
        .divider {
            width: 100%;
            height: 1px;
            background-color: #BEBEBE;
            box-shadow: 2px 2px 2px #BEBEBE;
            position: absolute;
            left: 0;
            bottom: -3px;
            margin: 0;
        }
    }
}
.entire-shelf {
    width: 100%;
    min-height: 325px;
    // height: 100%;
    background-color: #A4E49C;
}
.choose-material-box {
  border-radius: 10px;
  box-sizing: border-box;
  padding: 10px 10px 0 10px;
  margin: 10px 10px 10px;
  border: 2px dashed #BBBBBB;
  position: relative;
  .box-title {
    padding: 0 8px;
    box-sizing: border-box;
    position: absolute;
    background-color: #fff;
    color: #606266;
    left: 15px;
    top: -15px;
  }
}
.dropdown-custom-sty {
  margin: 0 10px 10px 0;
}
::v-deep .dropdown-custom-sty .el-button-group .el-button:nth-child(1) {
  width: 150px;
  display: inline-block;
  @include nowrap-ellipsis;
}
