import { getPdfUrlApi } from '@/apis/modules/in-storage/info'

export const infoPrintMixins = {
  methods: {
    handlePrint(id, printStyle /**  1 2 */, extraInfo = {}) {
      this.loading = true
      getPdfUrlApi({
        entity: {
          id,
          ...extraInfo
        },
        ...printStyle && { printStyle }
      }).then(res => {
        const { code, data } = res
        if (!code) {
          window.open(`${process.env.VUE_APP_BASE_API_WMS}${data}`)
        }
        this.loading = false
      }).catch(err => {
        console.log(err)
        this.loading = false
      })
    }
  }
}
