<template>
  <div class="app-container">
    <el-row class="clearfix">
      <div class="fl">
        <el-button @click="handleGoBack"></el-button>
      </div>
      <div class="fr">
        <el-button type="primary" @click="handleSave('filterItem')"></el-button>
        <el-button class="fr" type="primary" @click="handlePrintTemplate"></el-button>
      </div>
    </el-row>
    <el-collapse v-model="collapseValue" accordion>
      <el-collapse-item title="" name="1">
        <el-descriptions :column="2">
          <el-descriptions-item label="">{{ baseInfo.documentDate || '---' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ baseInfo.accountingDate || '---' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ baseInfo.receiptFactoryName || '---' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ baseInfo.billNumber || '---' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ baseInfo.receiptWarehouse || '---' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ baseInfo.movementType || '---' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ baseInfo.costCenterName || '---' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ baseInfo.ledgerSubjects || '---' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ baseInfo.supplierName || '---' }}</el-descriptions-item>
          <el-descriptions-item label="/">{{ baseInfo.buyerName || '---' }} {{ baseInfo.buyerTel || '---' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ baseInfo.asset || '---' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ baseInfo.purchaseOrderNumber || '---' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ baseInfo.network || '---' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ baseInfo.orderNumber || '---' }}</el-descriptions-item>
          <el-descriptions-item label="">{{ baseInfo.project || '---' }}</el-descriptions-item>
          <el-descriptions-item label="/">{{ baseInfo.contractCode || '---' }}</el-descriptions-item>
        </el-descriptions>
      </el-collapse-item>
    </el-collapse>
    <!--  -->
    <div style="margin-top: 20px;">
      <el-form ref="filterItem" :model="form" inline :rules="rules" label-width="80px" class="clearfix">
        <el-row class="clearfix fl">
          <el-form-item prop="type" label="">
            <el-radio-group v-model="form.type" disabled style="width: 178px;" @input="form.type === 1 && (form.isShowMoney = '')">
              <el-radio-button v-for="(item, index) in [{name: '', value: 1},{name: '', value: 2}]" :key="index" :label="item.value">{{ item.name }}</el-radio-button>
            </el-radio-group>
          </el-form-item>
          <el-form-item v-if="form.type === 2" prop="isShowMoney" label="">
            <el-radio-group v-model="form.isShowMoney" style="width: 178px;">
              <el-radio-button v-for="(item, index) in [{name: '', value: true}, {name: '', value: false}]" :key="index" :label="item.value">{{ item.name }}</el-radio-button>
            </el-radio-group>
          </el-form-item>
          <el-form-item prop="explanation" label="">
            <el-input v-model.trim="form.explanation" placeholder="" style="width: 178px;" />
          </el-form-item>
        </el-row>
        <el-row class="fr" style="width: 400px;">
          <el-descriptions :column="3" :colon="false">
            <el-descriptions-item>
              <el-link :underline="false" type="primary" @click="handleChooseLocation"></el-link>
            </el-descriptions-item>
            <el-descriptions-item label="">{{ baseInfo.stockInNumber }}</el-descriptions-item>
            <el-descriptions-item label="">{{ baseInfo.deliveredNumber }}</el-descriptions-item>
          </el-descriptions>
        </el-row>
      </el-form>
    </div>

    <!--  -->
    <el-row>
      <table-com
        ref="baseTableRef"
        :key="baseTableKey"
        :table-height-type="collapseValue ? 335 : 565"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :operation-width="120"
        :table-column-sort="false"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button type="primary" @click="handleModify(scope.row)"></el-button>
          <!-- <el-button type="warning" @click="handleModify(scope.row)"></el-button> -->
        </template>
      </table-com>
    </el-row>
    <pagination :page.sync="pageInfo.current" :limit.sync="pageInfo.size" :total="total" @pagination="init" />

    <!--  -->
    <edit-dialog
      v-model="dialogVisible"
      :dialog-form="dialogForm"
      :edit-type="editType"
      :extra-info="{stockId: baseInfo.id, isStrategic: form.type === 2}"
      @confirm="searchInit"
    />
    <!--  -->
    <storage-location
      v-model="visible"
      :extra-info="{stockInId: id, isStrategic: form.type === 2, warehouseSapId: baseInfo.receiptWarehouseId}"
      @confirm="selectedStorageLocation"
    />

  </div>
</template>

<script>
import tags from '@/mixins/tags'
import paginationPage from '@/mixins/paginationPage'
import EditDialog from './Dialog'
import { getListApi, getDetailListApi, submitDetailApi, saveDetailApi, deleteDetailApi, getDetailApi, recommendWarehousingApi } from '@/apis/modules/in-storage/info/receipt-detail'
import { infoPrintMixins } from '@/views/in-storage/info/print.mixins'
import { _debounce } from '@/utils'
import StorageLocation from './storageLocationExtra'

export default {
  components: {
    EditDialog,
    StorageLocation
  },
  mixins: [tags, paginationPage, infoPrintMixins],
  props: {
    // id
    id: {
      type: [String, Number],
      default: ''
    }
  },
  data() {
    return {
      title: this.$route.meta.title,
      loading: false,
      query: {},
      type: '',
      form: {
        type: '',
        isShowMoney: '',
        explanation: ''
      },
      rules: {
        type: [
          { required: true, message: '', trigger: 'blur' }
        ],
        isShowMoney: [
          { required: true, message: '', trigger: 'change' }
        ]
      },
      baseInfo: {},
      collapseValue: '1', // 
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'materialCode',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialName',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialSpec',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'materialAssessType_dictText',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'barcodeNumber',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'deliveredNumber',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unreceivedNumber',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'link',
          prop: 'lastStockInPosition',
          label: '',
          align: 'center',
          minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center',
          underline: (item) => false,
          disabled: (item) => false,
          handleClick: (item) => {
            // this.recommendWarehousing(item)
          }
        },
        {
          module: 'word',
          prop: 'materialCount',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        }
      ],
      columnSets: [],
      dialogForm: {},
      editType: 'add',
      dialogVisible: false,
      visible: false,
      baseTableKey: 1
    }
  },
  watch: {
    collapseValue: {
      handler() {
        this.$nextTick(() => {
          this.baseTableKey++
        })
      }
    }
  },
  async mounted() {
    await this.initInfo()
    await this.init()
    this.cacheStorageId()
  },
  methods: {
    // 
    cacheStorageId() {
      // 
      sessionStorage.setItem('bindRelation', JSON.stringify({}))
      // 
      if (this.baseInfo.id && this.tableData.length) {
        this.getStockInStorageId()
          .then(res => {
            // id
            const arr = res.filter(item => item.storageId)
            arr.forEach(item => {
              this.setBindRelation(item.materialCode, item.storageId)
            })
          })
      }
    },
    // key:  valid
    setBindRelation(key, val) {
      const obj = JSON.parse(sessionStorage.getItem('bindRelation'))
      if (obj) {
        if (obj[`${key}`]) {
          obj[`${key}`] = [...obj[`${key}`], val]
        } else {
          obj[`${key}`] = [val]
        }
        const str = JSON.stringify(obj)
        sessionStorage.setItem('bindRelation', str)
      }
    },
    // Tip 
    init() {
      this.loading = true
      const params = {
        stockInId: this.id,
        page: {
          ...this.pageInfo
        }
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { list, ...pageInfo } = data
            this.initPage(pageInfo)
            this.tableData = list.map(c => c)
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // Tip id()
    getStockInStorageId() {
      const promises = this.tableData.map(item => (getDetailListApi({ deliveryMaterialId: item.id, stockInId: this.baseInfo.id })))
      return Promise.all(promises)
        .then(res => {
          let arr = []
          res.forEach((item, index) => {
            arr = [...arr, ...item.data.map(c => ({ ...c, materialCode: this.tableData[index].materialCode }))]
          })
          return arr
        })
        .catch(err => {
          console.log('err', err)
        })
    },
    // Tip 
    initInfo() {
      const params = {
        entity: { id: this.id }
      }
      return new Promise((resolve, reject) => {
        getDetailApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.baseInfo = { ...data }
            this.form = {
              //  
              type: this.baseInfo.type,
              explanation: this.baseInfo.explanation,
              isShowMoney: this.baseInfo.isShowMoney
            }
            if (this.baseInfo.billNumber) {
              // tags
              this.setTagsTitleMixins(null, this.baseInfo.billNumber)
            }
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    // Tip 
    handleSave: _debounce(function(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const params = {
            entity: {
              id: this.id,
              ...this.form
            }
          }
          saveDetailApi(params).then(res => {
            const { code, data } = res
            if (!code) {
              this.$notify.success({
                title: '',
                message: `${data}`
              })
              // Tip 
              sessionStorage.removeItem('bindRelation')
              this.$store.dispatch('tagsView/delView', this.$route)
              // 
              this.$router.push({
                name: 'InStorageInfoDetail',
                params: {
                  id: this.$route.query.id // id
                }
              })
            }
          }).catch(err => {
            console.log(err)
          })
        } else {
          return false
        }
      })
    }, 800, false),
    // Tip id()
    confirmDialog(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = true
          const params = {
            entity: {
              ...this.form
            },
            wsmList: this.params.ids.map(i => {
              return {
                deliveryMaterialId: i
              }
            })
          }
          submitDetailApi(params).then(res => {
            const { code, data } = res
            if (!code) {
              this.$set(this.form, 'id', data)
            }
            this.loading = false
          }).catch(err => {
            console.log(err)
            this.loading = false
          })
        } else {
          return false
        }
      })
    },
    // 
    handleGoBack() {
      // Tip 
      sessionStorage.removeItem('bindRelation')
      // Tip 
      this.$store.dispatch('tagsView/delView', this.$route)
      // Tip 
      this.$router.push({
        name: 'InStorageInfoAdd',
        params: {
          id: this.$route.query.id // id
        }
      })
    },
    // Tip 
    handleReturnAndDelete() {
      return new Promise((resolve, reject) => {
        deleteDetailApi({
          entity: {
            id: this.id
          }
        }).then(res => {
          const { code } = res
          if (!code) {
            this.$nextTick(() => {
              // Tip 
              sessionStorage.removeItem('bindRelation')
              // Tip 
              this.$store.dispatch('tagsView/delView', this.$route)
              // Tip 
              this.$router.push({
                name: 'InStorageInfoAdd',
                params: {
                  id: this.$route.query.id // id
                }
              })
            })
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    // Tip 
    handleRowClick({ row, column, cell, event }) {
      if (column.label === '') {
        return false
      }
    },
    refresh() {
      // 
      this.init('clean')
    },
    handleAdd() {
      this.editType = 'add'
      this.dialogForm = {}
      this.dialogVisible = true
    },
    handleModify(item) {
      this.dialogForm = { ...item }
      this.editType = 'modify'
      this.dialogVisible = true
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.searchInit()
    },
    handlePrintTemplate() {
      this.handlePrint(this.baseInfo.id, '', { ...this.form })
    },
    recommendWarehousing(item) {
      this.$confirm(`${item.lastStockInPosition}?`, '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      })
        .then(() => {
          const params = {
            stockInId: this.id, // id
            deliveryMaterialId: item.id, // ID
            storageId: item.lastStockInStorageId // id
          }
          recommendWarehousingApi({
            ...params
          }).then(res => {
            const { code } = res
            if (!code) {
              this.$notify.success({
                title: '',
                message: ``
              })
              this.init()
            }
          }).catch(err => {
            console.log('err: ', err)
          })
        })
        .catch((e) => {
          this.$notify.info({
            title: '',
            message: ''
          })
        })
    },
    handleChooseLocation() {
      this.visible = true
      // this.$notify.warning({
      //   title: '',
      //   message: ''
      // })
    },
    selectedStorageLocation() {
      this.initInfo()
      this.init()
    }
  }
}
</script>

<style lang="scss" scoped>

</style>
