<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="65%"
    :show-close="false"
    direction="btt"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <template #title>
      <div style="display: flex;justify-content: space-between;align-items: center;">
        <span>{{ title }}</span>
        <div>
          <el-button :loading="btnLoading" type="primary" @click="handleConfirm('form')">{{ btnLoading ? ' ...' : ' ' }}</el-button>
          <el-button @click="beforeClose"> </el-button>
        </div>
      </div>
    </template>
    <div style="margin: 0 10px;">
      <el-row>
        <el-descriptions :border="true" direction="vertical" :column="8">
          <el-descriptions-item label="">{{ form.materialCode }}</el-descriptions-item>
          <el-descriptions-item label="">{{ form.materialName }}</el-descriptions-item>
          <el-descriptions-item label="">{{ form.materialSpec }}</el-descriptions-item>
          <el-descriptions-item label="">{{ form.materialAssessType_dictText }}</el-descriptions-item>
          <el-descriptions-item label="">{{ form.barcodeNumber }}</el-descriptions-item>
          <el-descriptions-item label="">{{ form.deliveredNumber }}</el-descriptions-item>
          <el-descriptions-item label="">{{ form.unitName }}</el-descriptions-item>
          <el-descriptions-item label="">{{ form.unreceivedNumber }}</el-descriptions-item>
        </el-descriptions>
      </el-row>

      <div style="margin: 10px 0;display: flex;justify-content: space-between;align-items: center;">
        <div style="font-size: 14px;">{{ tableData.length }}</div>
        <el-button type="primary" @click="handleCheckedAll"></el-button>
      </div>

      <table-com
        :key="refreshKey"
        ref="baseTableRef"
        :loading="loading"
        :index="true"
        :selection="false"
        :table-data="tableData"
        :column-list="columnList"
        :table-height-type="400"
        :operation-width="130"
        :expand-list="expandList"
        :table-column-sort="false"
        @rowClick="handleRowClick"
        @refresh="searchInit"
      >
        <template slot-scope="scope">
          <el-button v-if="!scope.row.deleteFlag" type="danger" @click="handleDelete(scope.index,scope.row )"></el-button>
          <el-button v-else type="primary" @click="handleRestore(scope.index, scope.row)"></el-button>
        </template>
      </table-com>
      <!-- <pagination
        :page.sync="pageInfo.current"
        :limit.sync="pageInfo.size"
        :total="total"
        @pagination="init"
      /> -->
    </div>
    <!-- <div class="drawer-footer clearfix">
      <el-button class="fr" @click="beforeClose"> </el-button>
      <el-button :loading="btnLoading" type="primary" class="fr" @click="handleConfirm('form')">{{ btnLoading ? ' ...' : ' ' }}</el-button>
    </div> -->

    <!--  -->
    <!-- <info-select
      v-model="selectedInfo.visible"
      :multiple="false"
      :extra-info="{}"
      @confirm="onSelectConfirm"
    /> -->
    <!-- :bind-relation="bindRelation" -->
    <storage-location
      v-if="selectedInfo.visible"
      v-model="selectedInfo.visible"
      :material-code="form.materialCode"
      :selected-info="selectedInfo.radio"
      :extra-info="{warehouseId: selectedInfo.radio.warehouseId, isStrategic: extraInfo.isStrategic, materialCode: form.materialCode}"
      @confirm="selectedStorageLocation"
    />
    <!-- <storage-location
      v-model="selectedInfo.visible"
      v-bind="{materialCode: form.materialCode, materialId: form.materialId, materialName: form.materialName}"
      @confirm="selectedStorageLocation"
    /> -->

  </el-drawer>
</template>

<script>
import paginationPage from '@/mixins/paginationPage'
// import InfoSelect from '@/components/ModalSelect/WarehouseLocation'
import StorageLocation from './storageLocation'
// import _ from 'lodash'
import { getDetailListApi as getListApi, delDetailListApi as getDeleteApi, submitDialogApi } from '@/apis/modules/in-storage/info/receipt-detail'

const INIT_FORM = () => ({
  deliveredNumber: '',
  deliveryNumber: '',
  materialCode: '',
  materialId: '',
  materialName: '',
  materialSpec: '',
  number: '',
  unitName: '',
  unreceivedNumber: ''
})

export default {
  name: 'InStorageInfoReceiptCom',
  components: {
    // InfoSelect,
    StorageLocation
  },
  mixins: [paginationPage],
  props: {
    value: {
      type: Boolean,
      default: false
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => {
        return { ...INIT_FORM() }
      }
    },
    extraInfo: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      title: '',
      refreshKey: new Date().getTime(),
      form: INIT_FORM(),
      loading: false,
      btnLoading: false,
      rules: {},
      query: {},
      tableData: [],
      columnList: [
        {
          module: 'word',
          prop: 'barCode',
          label: '',
          align: 'center',
          width: 200,
          // minWidth: 160,
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'number',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'word',
          prop: 'unitName',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          formatter: null,
          headerAlign: 'center'
        },
        {
          module: 'link',
          prop: 'storageName',
          label: '',
          align: 'center',
          minWidth: '',
          fixed: false,
          sortable: false,
          resizable: true,
          headerAlign: 'center',
          formatter: (scope) => {
            return scope.storageName ? scope.storageName : ''
          },
          disabled: (scope) => scope.deleteFlag,
          underline: false,
          handleClick: (scope, index) => {
            this.isCheckedAll = false // 
            this.selectedInfo.rowIndex = index
            this.selectedInfo.visible = true
            this.selectedInfo.radio = { ...scope }
          }
        }
      ],
      expandList: [],
      dialogVisible: false,
      dialogInfo: {},
      editInfoType: 'add',
      selectedInfo: {
        list: [],
        visible: false,
        radio: {},
        rowIndex: null
      },
      isCheckedAll: false
    }
  },
  computed: {
    dialogTitle() {
      return this.editType === 'add' ? `${this.title}` : `${this.title}`
    }
  },
  watch: {
    dialogForm: {
      handler: function(value) {
        if (this.editType === 'add') {
          this.form = { ...INIT_FORM() }
        } else {
          this.form = { ...value }
        }
      },
      immediate: true
    }
  },
  methods: {
    init() {
      this.loading = true
      return new Promise((resolve, reject) => {
        getListApi({
          ...this.query,
          stockInId: this.extraInfo.stockId,
          deliveryMaterialId: this.form.id
        }).then(res => {
          const { code, data } = res
          if (!code) {
            this.tableData = data.map(i => {
              return {
                ...i,
                storageName1: i.storageName,
                storageId1: i.storageId
              }
            })
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleConfirm(formName) {
      if (!this.form.id) {
        this.$notify.info({
          title: '',
          message: ''
        })
        return
      }
      const arr = this.tableData.filter(c => !c.deleteFlag)
      if (arr.length) {
        const bool = arr.every(c => !!c.storageId)
        const bool1 = arr.some(c => !!c.storageId)
        if (!bool && bool1) {
          this.$notify.warning({
            title: '',
            message: ''
          })
          return
        }
      }
      this.btnLoading = true
      const wsbList = this.tableData.map(i => {
        return {
          deliveryMaterialDetailId: i.id,
          stockInId: this.extraInfo.stockId,
          deliveryMaterialId: this.form.id,
          storageId: i.storageId,
          barCode: i.barCode,
          deleteFlag: i.deleteFlag
        }
      })
      submitDialogApi({
        wsbList
      }).then(res => {
        const { code } = res
        if (!code) {
          this.$emit('confirm')
          this.$emit('input', false)
          this.form = Object.assign({}, { ...INIT_FORM() })
        }
        this.btnLoading = false
      }).catch(err => {
        console.log(err)
        this.btnLoading = false
      })
    },
    // 
    getCacheAllmaterial() {
      const obj = JSON.parse(sessionStorage.getItem('bindRelation'))
      return Object.keys(obj)
    },
    handleRemove(item) {
      // const item = _.clonedeep(row)
      if (this.form.materialCode) {
        // 
        const isCache = this.getCacheAllmaterial().includes(`${this.form.materialCode}`)
        if (isCache) {
          // 
          const num = this.getBindRelation(this.form.materialCode).filter(c => c === item.storageId).length
          // 
          const ind = this.getBindRelation(this.form.materialCode).findIndex(c => c === item.storageId)
          let arr = []
          if (num === 1) {
            arr = this.getBindRelation(this.form.materialCode).filter(c => c !== item.storageId)
          }
          // 
          if (num > 1) {
            if (ind !== -1) {
              arr = this.getBindRelation(this.form.materialCode).filter((c, d) => d !== ind)
            }
          }
          if (!arr.length) {
            this.removeBindRelation(this.form.materialCode) // 
          } else {
            const obj = JSON.parse(sessionStorage.getItem('bindRelation'))
            obj[`${this.form.materialCode}`] = arr
            const str = JSON.stringify(obj)
            sessionStorage.setItem('bindRelation', str)
          }
        }
      }
    },
    //  
    handleCheckedAll() {
      this.isCheckedAll = true // 
      this.selectedInfo.rowIndex = ''
      this.selectedInfo.radio = this.form
      this.selectedInfo.visible = true
    },
    // 
    handleDelete(index, item) {
      // 
      this.handleRemove(item)
      this.$set(this.tableData, index, {
        ...item,
        storageId: '',
        storageName: '',
        deleteFlag: true
      })
      this.$notify.success({
        title: '',
        message: ''
      })
      // if (!item.deleteFlag) {
      //   return
      // }
      return
      this.$confirm(', ?', '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        getDeleteApi({
          id: item.id,
          stockInId: this.extraInfo.stockId,
          deliveryMaterialId: this.form.id
        }).then(res => {
          const { code } = res
          if (!code) {
            this.handleRemove(item)
            this.$notify.success({
              title: '',
              message: ''
            })
            this.$set(this.tableData, index, {
              ...this.tableData[index],
              storageId: '',
              storageName: '',
              isDel: !item.isDel
            })
          }
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '',
          message: ''
        })
      })
    },
    // 
    handleRestore(index, item) {
      this.$confirm(', ?', '', {
        confirmButtonText: ' ',
        cancelButtonText: ' ',
        type: 'warning',
        cancelButtonClass: 'btn-custom-cancel',
        lockScroll: false
      }).then(() => {
        this.$set(this.tableData, index, {
          ...this.tableData[index],
          storageName: item.storageName1 || '',
          storageId: item.storageId1 || '',
          deleteFlag: false
        })
      }).catch((e) => {
        console.log(e)
        this.$notify.info({
          title: '',
          message: ''
        })
      })
    },
    // onSelectConfirm(list) {
    //   if (list.length) {
    //     console.log(list)
    //     this.$set(this.tableData, this.selectedInfo.rowIndex, {
    //       ...this.tableData[this.selectedInfo.rowIndex],
    //       storageId: list[0].id,
    //       storageName: list[0].name
    //     })
    //   }
    // },
    // key:  valid
    setBindRelation(key, val) {
      const obj = JSON.parse(sessionStorage.getItem('bindRelation'))
      if (obj) {
        if (obj[`${key}`]) {
          obj[`${key}`] = [...obj[`${key}`], val]
        } else {
          obj[`${key}`] = [val]
        }
        const str = JSON.stringify(obj)
        sessionStorage.setItem('bindRelation', str)
      }
    },
    // id
    getBindRelation(key) {
      const obj = JSON.parse(sessionStorage.getItem('bindRelation'))
      return obj[`${key}`]
    },
    // (idid)
    hasBindRelation(key, val) {
      // id
      const arr = this.getBindRelation(key)
      return arr.includes(val)
    },
    // 
    removeBindRelation(key) {
      const obj = JSON.parse(sessionStorage.getItem('bindRelation'))
      if (obj && obj[`${key}`]) {
        delete obj[`${key}`]
        const str = JSON.stringify(obj)
        sessionStorage.setItem('bindRelation', str)
      }
    },
    // 
    selectedStorageLocation({ currentPosition, storageId }) {
      if (this.isCheckedAll) { // 
        // 
        this.removeBindRelation(this.dialogForm.materialCode)
        this.tableData = this.tableData.map(item => {
          // 
          this.setBindRelation(this.form.materialCode, storageId)
          return {
            ...item,
            storageId: storageId,
            storageName: currentPosition,
            storageId1: storageId,
            storageName1: currentPosition
          }
        })
      } else { // 
        // 
        this.setBindRelation(this.form.materialCode, storageId)
        this.$set(this.tableData, this.selectedInfo.rowIndex, {
          ...this.tableData[this.selectedInfo.rowIndex],
          storageId: storageId,
          storageName: currentPosition,
          storageId1: storageId,
          storageName1: currentPosition
        })
      }
    },
    handleAdd() {
      this.editInfoType = 'add'
      this.dialogInfo = {}
      this.dialogVisible = true
    },
    handleModify() {
      this.dialogInfo = { ...this.form }
      this.editInfoType = 'modify'
      this.dialogVisible = true
    },
    handleRowClick({ row, rowIndex, column, cell, event }) {
      if (column.label === '') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
      this.init()
    },
    initSelect() {
      this.tableData = []
      this.init()
        .then(() => {
        // 
          if (this.tableData.length && this.form.materialCode) {
            const obj = JSON.parse(sessionStorage.getItem('bindRelation'))
            const arr = this.tableData.filter(item => item.storageId).map(c => c.storageId)
            if (!arr.length) return
            obj[`${this.form.materialCode}`] = arr
            const str = JSON.stringify(obj)

            sessionStorage.setItem('bindRelation', str)
          }
        })
    },
    beforeClose(done) {
      this.$emit('input', false)
      this.tableData = []
      // 
      this.init()
        .then(() => {
          if (this.tableData.length && this.dialogForm.materialCode) {
            const obj = JSON.parse(sessionStorage.getItem('bindRelation'))
            const arr = this.tableData.filter(item => item.storageId).map(c => c.storageId)
            if (!arr.length) {
              this.removeBindRelation(this.dialogForm.materialCode)
              return
            }
            obj[`${this.dialogForm.materialCode}`] = arr
            const str = JSON.stringify(obj)

            sessionStorage.setItem('bindRelation', str)
          }
        })
      this.form = Object.assign({}, { ...INIT_FORM() })
      typeof done === 'function' && done()
    }
  }
}
</script>

<style lang='scss' scoped>
.container{
  width: 860px;
  margin: 0 auto;
  .left{
    width: 640px;
  }
  .right{
    margin-left: 20px;
    width: 180px;
  }
}
::v-deep:focus {
  outline: 0;
}
.scroll-drawer {
  height: calc(100vh - 160px);
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}

::v-deep .el-drawer__header {
  margin-bottom: 10px !important;
}

.drawer-footer{
  margin-top: 30px;
  margin-right: 30px;
}
</style>

